import App from './App'
import router from './router'
//载入http功能
import Resource from 'vue-resource'
import animate from 'animate.css'


Vue.config.productionTip = false
//在当前vue模块下调用resource功能
Vue.use(Resource);
window.onerror=function (){return true;}
new Vue({
  el: '#app',
  router,
  
  template: '<App/>',
  components: { App }
})



