<script>
import base from '../../public/base.css'
import register from '../../stylesheets/register.css'
import Header from '../main/Header'
export default {
    name:'register',
    data (){
        return {
            checked:true, 
        }
    },
    mounted(){
        //console.log(this.$route)
    },
    components:{
        'my-header':Header
    }

}
</script>



